@import url('https://fonts.googleapis.com/css?family=EB+Garamond&display=swap');

body {
  margin: 0;
  font-family: 'EB Garamond', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fefefa;
  color: #111;
  text-shadow: 0 0 3px #1113;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
}
ol, li {
  list-style: none;
  padding: 0;
}

.lemmata-list-container {
  float: left;
  width: 25%;
  height: 100%;
  margin: 0;
  padding: 10px;
  background-color: #e11a;
  box-sizing: border-box;
}

.language-list li {
  list-style: none;
  background-color: #1113;
  border: 1px dashed #11e8;
  padding: 5px;
  vertical-align: middle;
}
.language-list li:hover {
  list-style: none;
  background-color: #1118;
  border: 1px solid #11e8;
}

.language-list {
  display: block;
  text-decoration: none;
  width: 100%;
  vertical-align: middle;
}
.language-list.selected {
  display: block;
  text-decoration: none;
  width: 100%;
}

.lemma-list {
  box-sizing: border-box;
}
li.lemma-list.selected {
  background-color: #1116;
  color: red;
}

.lemma-editor-container {
  width: 75%;
  height: 100%;
  margin: 0 0 0 25%;
  padding: 10px;
  background-color: #00e7;
  box-sizing: border-box;
}